<template>
  <a-layout-content>
    <div class="caption">
      <a-breadcrumb>
        <a-breadcrumb-item>
          <router-link :to="{ name: 'dashboard' }">Главная</router-link>
        </a-breadcrumb-item>
        <a-breadcrumb-item>{{ textSymbolLimit(route, TITLE_LIMIT) }}</a-breadcrumb-item>
      </a-breadcrumb>
      <div class="caption-filling">
        <a-typography-title class="title" :level="3">
          {{
            textSymbolLimit(route, viewFullTitle ? route.length : TITLE_LIMIT)
          }}
          <br>
          <button v-if="route.length > TITLE_LIMIT" class="button-full" @click="viewFullTitle = !viewFullTitle">{{
              viewFullTitle ? 'Скрыть' : 'Показать полностью'
            }}
          </button>

        </a-typography-title>

        <a-button v-if="$route.name === 'hackathons'"
                  @click="$router.push({name:'hackathonEditor',params:{hackathonId:'new'}})" size="large"
                  type="primary">
          <template #icon>
            <plus-outlined></plus-outlined>
          </template>
          Создать хакатон
        </a-button>

        <a-button v-if="$route.name === 'hackathonEditor'"
                  @click="actions('saveHackathon')" size="large"
                  type="primary">
          <template #icon>
            <plus-outlined></plus-outlined>
          </template>
          Создать хакатон
        </a-button>

        <a-button v-if="$route.name === 'settings'" @click="actions('saveData')" class="save" type="primary"
                  size="large"
                  shape="round">
          Сохранить
        </a-button>
        <a-button v-if="$route.name === 'interviewEditor'" @click="actions('saveInterview')" class="save" type="primary"
                  size="large" shape="round">
          Сохранить
        </a-button>
        <a-button v-if="$route.name === 'currentMember'" @click="actions('saveMember')" class="save" type="primary"
                  size="large" shape="round">
          Сохранить
        </a-button>
        <a-button v-if="$route.name === 'ownEvents'" type="primary" size="large" shape="round"
                  class="create-button blue-buttons-ghost" @click="actions('createNewEvent')" ghost>
          <plus-outlined style="font-size: 18px"/>
          Создать мероприятие
        </a-button>
        <a-button v-if="$route.name === 'testingList'" type="primary" size="large" shape="round"
                  class="create-button blue-buttons-ghost" @click="actions('createNewStage')" ghost>
          <plus-outlined style="font-size: 18px"/>
          Создать
          {{
            stageMode === 'test' ? 'тестирование' : stageMode === 'exercise' ? 'практическое задание' : stageMode ===
            'interview' ? 'интервью' : stageMode === 'form' ? 'анкету' : stageMode === 'stream' ? 'трансляцию' : 'этап'
          }}
        </a-button>
        <a-button v-if="$route.name === 'post'" type="primary" size="large" shape="round"
                  class="create-button blue-buttons-ghost" @click="actions('writingMessage')" ghost>
          Написать письмо
        </a-button>

        <div class="buttons-container" v-if="$route.name === 'members'">
          <a-button shape="round" size="large" @click="$refs.download.click()">

            <a :href="appConfig.domain + '/client/all/xls'" hidden ref="download"
               :download="appConfig.domain + '/client/all/xls'"></a>

            Скачать базу
            <down-outlined style="font-size: 10px;"/>
          </a-button>
          <a-button type="primary" size="large" shape="round" @click="$router.push({
            name: 'currentMember', params: {
              id: 'new'
            }
          })">Добавить участника
          </a-button>
        </div>
      </div>
    </div>
    <slot name="content"></slot>
    <stream-modal :stream="stream" v-if="stream&&viewStreamModal">
    </stream-modal>

    <button @click="fetchLastStream" class="stream-btn">

    </button>
  </a-layout-content>
</template>

<script>
import {PlusOutlined, DownOutlined} from "@ant-design/icons-vue";
import {appConfig} from "@/lib/appConfig";
import {textSymbolLimit} from "../../lib/textSymbolLimit";
import StreamModal from "@/components/stream/StreamModal.vue";

export default {
  name: "PersonalCaption",
  components: {PlusOutlined, DownOutlined, StreamModal},
  computed: {
    stageMode() {
      return this.$store.getters.stageMode;
    },
    stream() {
      return this.$store.getters.stream;
    },
    viewStreamModal() {
      return this.$store.getters.viewStreamModal;
    }
  },
  methods: {
    textSymbolLimit,
    actions(eventName) {
      document.dispatchEvent(new Event(eventName));
    },
    fetchLastStream() {
      this.$store.dispatch('fetchLastStream');
    }
  },
  data() {
    return {
      appConfig,
      TITLE_LIMIT: 100,
      viewFullTitle: false,
      route:
          this.$route.name === "settings"
              ? "Настройки"
              : this.$route.name === "myEvents"
                  ? "Мои мероприятия"
                  : this.$route.name === "allEvents"
                      ? "Все мероприятия"
                      : this.$route.name === "separateEvent"
                          ? "Мероприятие"
                          : this.$route.name === "testing"
                              ? "Тестирование"
                              : this.$route.name === "interview"
                                  ? "Видео-интервью"
                                  : this.$route.name === "form"
                                      ? "Анкета"
                                      : this.$route.name === "exercise"
                                          ? "Практическое задание"
                                          : this.$route.name === "history"
                                              ? "История отборов"
                                              : this.$route.name === "users"
                                                  ? "Пользователи"
                                                  : this.$route.name === "ownEvents"
                                                      ? "Созданные мероприятия"
                                                      : this.$route.name === "testingList"
                                                          ? "Этапы"
                                                          : this.$route.name === "raterEvents"
                                                              ? "Оценка этапов мероприятий"
                                                              : this.$route.name === "testingEditor"
                                                                  ? ((this.$route.params.testingId === 'new' ? 'Создание' : "Редактирование") + ' ' + (this.$route.params.mode === 'test' ? 'тестирования' : this.$route.params.mode === 'exercise' ? 'практического задания' : this.$route.params.mode === 'form' ? 'анкеты' : '')) :
                                                                  this.$route.name === 'streamEditor' ? `${this.$route.params?.streamId === 'new' ? 'Создание' : "Редактирование"} трансляции`
                                                                      : this.$route.name === "members"
                                                                          ? "Участники"
                                                                          : this.$route.name === "raters"
                                                                              ? "Оценщики"
                                                                              : this.$route.name === "post"
                                                                                  ? "Почтовый клиент"
                                                                                  : this.$route.name === "dashboard"
                                                                                      ? "Личный кабинет"
                                                                                      : this.$route.name === "raterCurrentEvent"
                                                                                          ? "Список участников"
                                                                                          : this.$route.name === "interviewEditor"
                                                                                              ? "Видео-интервью"
                                                                                              : this.$route.name === "currentMember"
                                                                                                  ? "Участник" :
                                                                                                  this.$route.name === "hackathons"
                                                                                                      ? "Хакатон" :
                                                                                                      this.$route.name === 'hackathonEditor' ? `${this.$route.params?.hackathonId === 'new' ? 'Создание' : "Редактирование"} хакатона` :
                                                                                                          "",

    };
  },
  beforeMount() {
    document.addEventListener('setTitle', (event) => {
      this.route = event.detail;
    })
  }
};
</script>

<style scoped>
.caption {
  padding: 12px 24px 16px;
  box-shadow: inset 0px -1px 0px #f0f0f0;
}

.caption-filling {
  margin-top: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.save {
}

.title {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 20px;
}

.button-full {
  font-size: 14px !important;
  font-weight: 600;
  cursor: pointer;
}

.stream-btn {
  width: 60px;
  height: 60px;
  border-radius: 100%;
  background: url("@/assets/icons/stream-demonstration.svg");
  background-size: contain;
  background-repeat: no-repeat;
  position: fixed;
  z-index: 100;
  right: 30px;
  bottom: 30px;
  cursor: pointer;
}

@media (max-width: 700px) {
  .caption {
    padding: 12px 10px 16px 10px;
  }

  .stream-btn {
    width: 40px;
    height: 40px;
    background: url("@/assets/icons/stream-demonstration.svg");
    background-size: contain;
    background-repeat: no-repeat;
    right: 16px;
    bottom: 16px;
  }
}

.buttons-container {
  display: flex;
  gap: 29px;
}
</style>
